import { Directive, EventEmitter, HostListener, Output } from '@angular/core';

@Directive({
  selector: '[swipe]',
  standalone: true
})
export class SwipeDirective {
  @Output() swipeleft = new EventEmitter<void>();
  @Output() swiperight = new EventEmitter<void>();
  @Output() swipeup = new EventEmitter<void>();
  @Output() swipedown = new EventEmitter<void>();

  private swipeCoord?: [number, number];
  private swipeTime?: number;

  @HostListener('touchstart', ['$event'])
  onTouchStart(e: TouchEvent): void {
    this.swipe(e, 'start');
  }

  @HostListener('touchend', ['$event'])
  onTouchEnd(e: TouchEvent): void {
    this.swipe(e, 'end');
  }

  private swipe(e: TouchEvent, when: string): void {
    const coord: [number, number] = [
      e.changedTouches[0].clientX,
      e.changedTouches[0].clientY
    ];
    const time = new Date().getTime();

    if (when === 'start') {
      this.swipeCoord = coord;
      this.swipeTime = time;
    } else if (when === 'end') {
      const direction = [
        coord[0] - (this.swipeCoord?.[0] ?? 0),
        coord[1] - (this.swipeCoord?.[1] ?? 0)
      ];
      const duration = time - (this.swipeTime ?? 0);

      if (
        duration < 1000 &&
        Math.abs(direction[0]) > 30 &&
        Math.abs(direction[0]) > Math.abs(direction[1] * 3)
      ) {
        if (direction[0] < 0) {
          this.swipeleft.emit();
        } else {
          this.swiperight.emit();
        }
      } else if (
        duration < 1000 &&
        Math.abs(direction[1]) > 30 &&
        Math.abs(direction[1]) > Math.abs(direction[0] * 3)
      ) {
        if (direction[1] < 0) {
          this.swipeup.emit();
        } else {
          this.swipedown.emit();
        }
      }
    }
  }
}
