import { Component, OnInit, OnChanges, AfterViewInit, Input, SimpleChanges } from '@angular/core';
import { HttpClient } from '@angular/common/http';

interface Specification {
  id: string;
  title: string;
  description: string;
  priority: string;
  status: string;
  details?: string;
}

interface ProjectSpec {
  name: string;
  displayName: string;
  description: string;
  overview: any;
  specifications: Specification[];
  systemArchitecture: any;
  requirements: any[];
}

@Component({
  selector: 'app-spec-doc',
  templateUrl: './spec-doc.component.html',
  styleUrls: ['./spec-doc.component.scss'],
  standalone: false
})
export class SpecDocComponent implements OnInit, OnChanges, AfterViewInit {
  @Input() folderType: 'calendar' | 'note' = 'calendar';

  projectSpecs: { [key: string]: ProjectSpec } = {};
  currentProjectSpec: ProjectSpec | null = null;

  constructor(private http: HttpClient) { }

  ngOnInit(): void {
    this.loadProjectSpecs();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['folderType'] && !changes['folderType'].firstChange) {
      this.updateCurrentSpec();
    }
  }

  ngAfterViewInit(): void {
    // ì´ˆê¸° ë°ì´í„° ë¡œë“œ í›„ ì—…ë°ì´íŠ¸ í™•ì¸
    if (Object.keys(this.projectSpecs).length > 0) {
      this.updateCurrentSpec();
    }
  }

  loadProjectSpecs(): void {
    this.http.get('/assets/data/project-specs.json').subscribe(
      (data: any) => {
        this.projectSpecs = data;
        this.updateCurrentSpec();
      },
      (error: any) => {
        console.error('Failed to load spec data:', error);
      }
    );
  }

  updateCurrentSpec(): void {
    this.currentProjectSpec = this.projectSpecs[this.folderType] || null;
  }

  getStatusClass(status: string): string {
    switch(status) {
      case 'ì§„í–‰ì¤‘': return 'in-progress';
      case 'ì™„ë£Œ': return 'completed';
      case 'ê³„íš': return 'planned';
      default: return '';
    }
  }

  getPriorityClass(priority: string): string {
    switch(priority) {
      case 'ë†’ìŒ': return 'high';
      case 'ì¤‘ê°„': return 'medium';
      case 'ë‚®ìŒ': return 'low';
      default: return '';
    }
  }
}
