import { Injectable } from '@angular/core';
import { BehaviorSubject, fromEvent, Observable } from 'rxjs';
import { debounceTime, map, startWith } from 'rxjs/operators';

export interface DeviceInfo {
  isMobile: boolean;
  isTablet: boolean;
  isDesktop: boolean;
  screenWidth: number;
  screenHeight: number;
}

@Injectable({
  providedIn: 'root'
})
export class DeviceDetectService {
  private deviceInfoSubject = new BehaviorSubject<DeviceInfo>(this.detectDevice());
  public deviceInfo$: Observable<DeviceInfo> = this.deviceInfoSubject.asObservable();

  constructor() {
    // ìœˆë„ìš° ë¦¬ì‚¬ì´ì¦ˆ ì´ë²¤íŠ¸ ê°ì§€
    fromEvent(window, 'resize')
      .pipe(
        debounceTime(300),
        map(() => this.detectDevice()),
        startWith(this.detectDevice())
      )
      .subscribe(info => this.deviceInfoSubject.next(info));
  }

  private detectDevice(): DeviceInfo {
    const width = window.innerWidth;
    const height = window.innerHeight;
    
    const isMobile = width < 768;
    const isTablet = width >= 768 && width < 1024;
    const isDesktop = width >= 1024;

    return {
      isMobile,
      isTablet,
      isDesktop,
      screenWidth: width,
      screenHeight: height
    };
  }

  get isMobile(): boolean {
    return this.deviceInfoSubject.value.isMobile;
  }

  get isTablet(): boolean {
    return this.deviceInfoSubject.value.isTablet;
  }

  get isDesktop(): boolean {
    return this.deviceInfoSubject.value.isDesktop;
  }

  get currentDeviceInfo(): DeviceInfo {
    return this.deviceInfoSubject.value;
  }
}
