import { HttpClient } from '@angular/common/http';
import { Component, ComponentRef, ElementRef, EventEmitter, HostListener, inject, input, Input, OnDestroy, OnInit, Output, Type, ViewChild, ViewContainerRef } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatIconModule } from '@angular/material/icon';
import { MatButtonModule } from '@angular/material/button';
import { Router } from '@angular/router';
import { Subscription, take, fromEvent, debounceTime } from 'rxjs';
import { CommonModule } from '@angular/common';

@Component({
  selector: 'app-ia-canvas',
  templateUrl: './ia-canvas.component.html',
  styleUrls: ['./ia-canvas.component.scss'],
  standalone: false
})
export class IaCanvasComponent implements OnInit, OnDestroy {
  @ViewChild('container', {static: true, read: ViewContainerRef}) container!: ViewContainerRef;
  @ViewChild('intentName', {static: true}) intentNameInput!: ElementRef<HTMLInputElement>;
  @ViewChild('fileInput') fileInput!: ElementRef<HTMLInputElement>;
  @Output() playSimulator = new EventEmitter<void>();
  @Output() zoomLevelChange = new EventEmitter<number>();
  @Output() penModeChange = new EventEmitter<boolean>();
  
  public width = input<number>(1200);
  public height = input<number>(0);

  @Input() pages: any[] = [];
  @Input() userFlows: any[] = [];
  @Input() viewMode: string = 'canvas';
  @Input() set zoomLevel(value: number) {
    this._zoomLevel = value;
  }
  get zoomLevel(): number {
    return this._zoomLevel;
  }
  
  @Input() set isPenMode(value: boolean) {
    this._isPenMode = value;
  }
  get isPenMode(): boolean {
    return this._isPenMode;
  }

  private _zoomLevel = 100;
  private _isPenMode = false;
  private subscription = new Subscription();

  // íŒ¬ ê¸°ëŠ¥ ë³€ìˆ˜
  private offsetX = 0;
  private offsetY = 0;
  public isPanning = false;
  private panStartX = 0;
  private panStartY = 0;

  private dialog = inject(MatDialog);
  private snackbar = inject(MatSnackBar);
  private router = inject(Router);
  private httpClient = inject(HttpClient);

  constructor() {}

  ngOnInit(): void {
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  zoomIn() {
    if (this.zoomLevel < 200) {
      this.zoomLevel = Math.min(200, this.zoomLevel + 10);
      this.zoomLevelChange.emit(this.zoomLevel);
    }
  }

  zoomOut() {
    if (this.zoomLevel > 50) {
      this.zoomLevel = Math.max(50, this.zoomLevel - 10);
      this.zoomLevelChange.emit(this.zoomLevel);
    }
  }

  resetZoom() {
    this.zoomLevel = 100;
    this.zoomLevelChange.emit(this.zoomLevel);
  }

  onWheel(event: WheelEvent) {
    if (event.ctrlKey || event.metaKey) {
      event.preventDefault();
      
      if (event.deltaY < 0) {
        this.zoomIn();
      } else {
        this.zoomOut();
      }
    }
  }

  @HostListener('window:keydown', ['$event'])
  handleKeyboard(event: KeyboardEvent) {
    if (event.ctrlKey || event.metaKey) {
      if (event.key === '+' || event.key === '=') {
        event.preventDefault();
        this.zoomIn();
      } else if (event.key === '-') {
        event.preventDefault();
        this.zoomOut();
      } else if (event.key === '0') {
        event.preventDefault();
        this.resetZoom();
      }
    }
  }

  // íŒ¬ ê¸°ëŠ¥
  onCanvasMouseDown(event: MouseEvent) {
    if ((event.shiftKey || this.isPenMode) && event.button === 0) {
      this.isPanning = true;
      this.panStartX = event.clientX;
      this.panStartY = event.clientY;
      event.preventDefault();
    }
  }

  togglePenMode() {
    this.isPenMode = !this.isPenMode;
    this.penModeChange.emit(this.isPenMode);
  }

  onCanvasMouseMove(event: MouseEvent) {
    if (this.isPanning) {
      const deltaX = event.clientX - this.panStartX;
      const deltaY = event.clientY - this.panStartY;

      this.offsetX += deltaX;
      this.offsetY += deltaY;

      this.panStartX = event.clientX;
      this.panStartY = event.clientY;
    }
  }

  onCanvasMouseUp() {
    this.isPanning = false;
  }

  getTransformStyle(): string {
    return `translate(${this.offsetX}px, ${this.offsetY}px) scale(${this.zoomLevel / 100})`;
  }
}
