import { Component, OnInit, OnChanges, AfterViewInit, Input, SimpleChanges } from '@angular/core';
import { HttpClient } from '@angular/common/http';

interface IANode {
  id: string;
  name: string;
  description: string;
  components?: string[];
  children?: IANode[];
}

interface UserFlow {
  name: string;
  steps: string[];
}

interface ProjectIA {
  pages: IANode[];
  userFlows: UserFlow[];
}

interface ProjectData {
  name: string;
  ia: ProjectIA;
}

@Component({
  selector: 'app-ia-doc',
  templateUrl: './ia-doc.component.html',
  styleUrls: ['./ia-doc.component.scss'],
  standalone: false
})
export class IaDocComponent implements OnInit, OnChanges, AfterViewInit {
  @Input() folderType: 'calendar' | 'note' = 'calendar';

  projectSpecs: { [key: string]: any } = {};
  currentProjectData: ProjectData | null = null;

  expandedNodes: Set<string> = new Set();
  viewMode: 'canvas' | 'tree' = 'canvas'; // ê¸°ë³¸ ëª¨ë“œ

  // Canvas Toolbar ìƒíƒœ
  canvasZoomLevel = 100;
  isPenMode = false;

  constructor(private http: HttpClient) { }

  ngOnInit(): void {
    this.loadProjectSpecs();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['folderType'] && !changes['folderType'].firstChange) {
      this.updateCurrentData();
    }
  }

  ngAfterViewInit(): void {
    if (Object.keys(this.projectSpecs).length > 0) {
      this.updateCurrentData();
    }
  }

  loadProjectSpecs(): void {
    this.http.get('/assets/data/project-specs.json').subscribe(
      (data: any) => {
        this.projectSpecs = data;
        this.updateCurrentData();
      },
      (error: any) => {
        console.error('Failed to load IA data:', error);
      }
    );
  }

  updateCurrentData(): void {
    const project = this.projectSpecs[this.folderType];
    if (project) {
      this.currentProjectData = {
        name: project.name,
        ia: project.ia
      };
      // ì²« ëª‡ ê°œ ë…¸ë“œ ì „ê°œ
      if (this.currentProjectData.ia.pages.length > 0) {
        this.currentProjectData.ia.pages.forEach((page, index) => {
          if (index < 3) {
            this.expandedNodes.add(page.id);
          }
        });
      }
    }
  }

  toggleNode(id: string): void {
    if (this.expandedNodes.has(id)) {
      this.expandedNodes.delete(id);
    } else {
      this.expandedNodes.add(id);
    }
  }

  isNodeExpanded(id: string): boolean {
    return this.expandedNodes.has(id);
  }

  renderNodes(nodes: IANode[], level: number = 0): any[] {
    const result: any[] = [];
    nodes.forEach(node => {
      result.push({ node, level });
      if (node.children && this.isNodeExpanded(node.id)) {
        result.push(...this.renderNodes(node.children, level + 1));
      }
    });
    return result;
  }

  hasChildren(node: IANode): boolean {
    return !!(node.children && node.children.length > 0);
  }

  onViewModeChange(mode: string): void {
    if (mode === 'canvas' || mode === 'tree') {
      this.viewMode = mode;
    }
  }

  zoomIn(): void {
    if (this.canvasZoomLevel < 200) {
      this.canvasZoomLevel = Math.min(200, this.canvasZoomLevel + 10);
    }
  }

  zoomOut(): void {
    if (this.canvasZoomLevel > 50) {
      this.canvasZoomLevel = Math.max(50, this.canvasZoomLevel - 10);
    }
  }

  resetZoom(): void {
    this.canvasZoomLevel = 100;
  }

  togglePenMode(): void {
    this.isPenMode = !this.isPenMode;
  }
}
