import { Component, OnInit, OnChanges, AfterViewInit, Input, SimpleChanges } from '@angular/core';
import { HttpClient } from '@angular/common/http';

interface WBSItem {
  id: string;
  name: string;
  startDate: string;
  endDate: string;
  progress: number;
  responsible: string;
  subtasks?: WBSItem[];
}

interface ProjectWBS {
  tasks: WBSItem[];
}

interface ProjectData {
  name: string;
  wbs: ProjectWBS;
}

@Component({
  selector: 'app-wbs-doc',
  templateUrl: './wbs-doc.component.html',
  styleUrls: ['./wbs-doc.component.scss'],
  standalone: false
})
export class WbsDocComponent implements OnInit, OnChanges, AfterViewInit {
  @Input() folderType: 'calendar' | 'note' = 'calendar';

  projectSpecs: { [key: string]: any } = {};
  currentProjectData: ProjectData | null = null;

  expandedItems: Set<string> = new Set();

  constructor(private http: HttpClient) { }

  ngOnInit(): void {
    this.loadProjectSpecs();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['folderType'] && !changes['folderType'].firstChange) {
      this.updateCurrentData();
    }
  }

  ngAfterViewInit(): void {
    if (Object.keys(this.projectSpecs).length > 0) {
      this.updateCurrentData();
    }
  }

  loadProjectSpecs(): void {
    this.http.get('/assets/data/project-specs.json').subscribe(
      (data: any) => {
        this.projectSpecs = data;
        this.updateCurrentData();
      },
      (error: any) => {
        console.error('Failed to load WBS data:', error);
      }
    );
  }

  updateCurrentData(): void {
    const project = this.projectSpecs[this.folderType];
    if (project) {
      this.currentProjectData = {
        name: project.name,
        wbs: project.wbs
      };
      // ì²« ë²ˆì§¸ í•­ëª©ë“¤ ì „ê°œ
      if (this.currentProjectData.wbs.tasks.length > 0) {
        this.expandedItems.add(this.currentProjectData.wbs.tasks[0].id);
      }
    }
  }

  toggleExpand(id: string): void {
    if (this.expandedItems.has(id)) {
      this.expandedItems.delete(id);
    } else {
      this.expandedItems.add(id);
    }
  }

  isExpanded(id: string): boolean {
    return this.expandedItems.has(id);
  }

  getProgressColor(progress: number): string {
    if (progress >= 75) return '#10b981';
    if (progress >= 50) return '#f59e0b';
    if (progress >= 25) return '#f97316';
    return '#ef4444';
  }

  renderItems(items: WBSItem[]): any[] {
    const result: any[] = [];
    items.forEach(item => {
      result.push(item);
      if (item.subtasks && this.isExpanded(item.id)) {
        result.push(...this.renderItems(item.subtasks));
      }
    });
    return result;
  }

  getLevel(items: WBSItem[], targetId: string, currentLevel: number = 0): number {
    for (let item of items) {
      if (item.id === targetId) {
        return currentLevel;
      }
      if (item.subtasks) {
        const level = this.getLevel(item.subtasks, targetId, currentLevel + 1);
        if (level >= 0) return level;
      }
    }
    return 0;
  }

  hasChildren(item: WBSItem): boolean {
    return !!(item.subtasks && item.subtasks.length > 0);
  }

  getItemLevel(item: WBSItem): number {
    if (this.currentProjectData?.wbs.tasks) {
      return this.getLevel(this.currentProjectData.wbs.tasks, item.id);
    }
    return 0;
  }
}
